/* Color Scheme: https://coolors.co/0a2239-80d3f2-fa8334-fbd905-225560 */

body {
  --color1: #fbd905;
  --color2: #80d3f2;
  --color3: #0a2239;
  --color4: #fa8334;
  --color5: #225560;

  margin: 0;
  padding: 0;
  font-family: sans-serif;

  background-color: var(--color3);
  color: var(--color2);
}

section {
  margin: 5vh 10vw;
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(251, 217, 5, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

h1 {
  color: var(--color1);
}

h3,
h2,
a {
  color: var(--color1);
}

p {
  color: var(--color2);
}

.card-container {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  padding: 10px;
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  background-color: var(--color5);
  border-radius: 10px;
  width: 80vw;
  max-width: 300px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.tag-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.tag {
  margin: 5px;
  padding: 3px 6px;
  background-color: var(--color3);
  border: 2px ridge var(--color1);
  border-radius: 40px;
  color: var(--color1);
  font-weight: 700;

  font-size: 14px;
}

#calendar {
  text-align: center;
}

#calendar iframe {
  width: 100%;
}

/* Extra small devices (phones, less than 768px) */
@media (max-width: 768px) {
  h1 {
    font-size: 20px;
  }

  h2 {
    font-size: 18px;
  }

  #calendar {
    overflow: hidden;
  }

  #calendar iframe {
    width: 250vw;
    height: 350px;
  }
}
